<template>
  <v-card tile color="grey lighten-2" height="100vh">
    <v-card-text>
      <!-- <v-container fluid> -->
      <v-row no-gutters class="mt-5" justify="center" align="center">
        <v-col class="hidden-sm-and-down" sm="4" md="5">
          <v-card
            tile
            elevation="8"
            height="500"
            class="d-flex px-4 flex-column align-center"
            color="#253c88"
          >
            <v-img
              contain
              :src="require('@/assets/eduRoom-white2.png')"
            ></v-img>
          </v-card>
        </v-col>
        <v-col xs="12" sm="8" md="5">
          <v-card
            elevation="8"
            tile
            :loading="loading"
            height="500"
            color="base"
          >
            <v-card-title>
              <h3>Register here !</h3>
            </v-card-title>
            <v-card-subtitle>
              Welcome, please register to login!
            </v-card-subtitle>
            <v-form @submit.prevent="register" ref="myForm" v-model="valid">
              <v-container class="px-7">
                <v-text-field
                  v-model="form.username"
                  :rules="[rules.required, rules.minLength, rules.maxLength]"
                  filled
                  dense
                  outlined
                  label="Username"
                  autocomplete="username"
                  prepend-inner-icon="mdi-account"
                ></v-text-field>
                <v-text-field
                  v-model="form.email"
                  :rules="[rules.required, rules.email]"
                  filled
                  dense
                  outlined
                  label="Email"
                  prepend-inner-icon="mdi-email"
                ></v-text-field>
                <v-text-field
                  v-model="form.password"
                  :rules="[rules.required]"
                  dense
                  filled
                  outlined
                  label="Password"
                  :type="showPassword ? 'text' : 'password'"
                  autocomplete="new-password"
                  prepend-inner-icon="mdi-lock"
                >
                  <template v-slot:append>
                    <v-icon
                      tabindex="-1"
                      @click="showPassword = !showPassword"
                      v-if="showPassword"
                      >mdi-eye-off</v-icon
                    >
                    <v-icon
                      tabindex="-1"
                      @click="showPassword = !showPassword"
                      v-if="!showPassword"
                      >mdi-eye</v-icon
                    >
                  </template>
                </v-text-field>
                <v-text-field
                  v-model="form.confirm_password"
                  :rules="[rules.required, rules.passwordMatch]"
                  dense
                  filled
                  outlined
                  label="Confirm password"
                  :type="showPassword ? 'text' : 'password'"
                  autocomplete="new-password"
                  prepend-inner-icon="mdi-lock"
                ></v-text-field>
                <div class="d-flex justify-space-between mb-5">
                  <v-btn outlined color="primary" to="/login">Login</v-btn>
                  <v-btn class="primary primary--shadow" type="submit"
                    >Register</v-btn
                  >
                </div>
              </v-container>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
      <!-- </v-container> -->
    </v-card-text>
  </v-card>
</template>

<script>
import { onMounted, reactive, ref } from "@vue/composition-api";
export default {
  name: "Register",
  setup(_, { root }) {
    const valid = ref(false);
    const myForm = ref(null);
    const loading = ref(false);
    const showPassword = ref(false);
    const rules = reactive({
      required: (value) => !!value || "Data harus diisi.",
      minLength: (value) =>
        value.length >= 6 || "Tidak boleh kurang dari 6 huruf",
      maxLength: (value) =>
        value.length <= 20 || "Tidak boleh lebih dari 20 huruf",
      passwordMatch: (value) =>
        (!!value && value) === form.password || "Password tidak sama",
      email: (value) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "Email tidak valid.";
      },
    });
    const form = reactive({
      username: "",
      email: "",
      password: "",
      confirm_password: "",
      lat: "",
      lng: "",
    });
    onMounted(() => {
      root.$getLocation({ enableHighAccuracy: true }).then(({ lat, lng }) => {
        form.lat = lat;
        form.lng = lng;
      });
    });
    const register = () => {
      if (valid.value) {
        loading.value = "primary";
        root.axios
          .post("auth/register", form)
          .then((res) => {
            console.log(res);
            root.bus.$emit("showSnackbar", {
              message: "Berhasil registrasi",
              show: true,
            });
            root.$router.push("/login");
          })
          .catch((err) => {
            root.bus.$emit("showSnackbar", {
              message: err.response.data.message,
              show: true,
              color: "error",
            });
          })
          .then(() => (loading.value = false));
      } else {
        myForm.value.validate();
      }
    };
    return { valid, myForm, form, rules, showPassword, loading, register };
  },
};
</script>

